.container {
  perspective: 1920px;
  perspective-origin: 50% 0px;
  width: 100%;
  height: 70%;
  color: black;
  text-align: center;
  position: relative;
  input {
    display: none;
  }
  .tab {
    position: absolute;
    width: 10%;
    height: 28%;
    background: pink;
    right: 0;
    line-height: 70px;
    font-weight: 300;
    z-index: 20;
    border-radius: 2rem;
    border: 1px solid black;
    @media (max-width: 768px) {
      border-radius: 1.5rem;
    }
    &:nth-child(1) {
      top: 0;
      background: #f1f6ff;
    }
    &:nth-child(2) {
      top: 30%;
      background: #ebf5fd;
    }
    &:nth-child(3) {
      top: 60%;
      background: #b0c7f8;
    }
    &:nth-child(4) {
      top: 90%;
      background: #6aa2c7;
    }
  }
  .cube {
    position: relative;
    width: 75%;
    height: 100%;
    margin-left: 7.7%;
    transform-origin: 0% 50%;
    transform-style: preserve-3d;
    transition: transform 0.5s ease-in;
    .content {
      position: absolute;
      width: 100%;
      height: 100%;
      position: absolute;
      border-radius: 2rem;
      border: 1px solid black;

      h1 {
        font-size: 2rem;
        font-weight: 300;
      }
      p {
        font-size: 1rem;
      }
      &:nth-child(1) {
        transform: rotateX(-270deg) translateY(-50%);
        transform-origin: top left;
        background-color: #f1f6ff;
      }
      &:nth-child(2) {
        transform: translateZ(295px);
        background-color: #ebf5fd;
      }
      &:nth-child(3) {
        transform: rotateX(-90deg) translateY(50%);
        transform-origin: bottom center;
        background-color: #b0c7f8;
      }
      &:nth-child(4) {
        transform: rotateX(180deg) translateY(100%) translateZ(295px);
        transform-origin: bottom center;
        background-color: #6aa2c7;
      }
    }
  }
}

#tabtop:checked ~ .cube {
  transform: rotateX(-90deg);
}

#tabfront:checked ~ .cube {
  transform: rotateX(0deg);
}

#tabbottom:checked ~ .cube {
  transform: rotateX(90deg);
}
#tablast:checked ~ .cube {
  transform: rotateX(180deg);
}
